* {
  box-sizing: content-box;
  outline: none;
}

html {
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
}

:root {
  font-size: 10px;
  line-height: 1.36;
  --bg: #121C27;
  --bg-alt: #0E161F;
  --color: #F5F6F7;
  --red: #E61C3E;
  --great-transition: all 200ms ease;
}

body {
  position: relative;
  flex-direction: column;
  display: flex;
  background-color: #fff;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  min-height: 100vh;
  background-color: var(--bg);
  color: var(--color);
  font-size: 1.8rem;
  line-height: 1.6;
  font-family: "Ubuntu", sans-serif;
  font-weight: 300;
}

#footer {
  width: 100%;
  box-sizing: border-box;
  position: relative;
  margin-top: auto;
}

h1, h2, h3, h4, h5, h6 {
  display: block;
  margin: 0;
  padding: 0;
  font-weight: 400;
}

p {
  display: block;
  margin: 0;
  padding: 0;
}

.h1, .h2, .h3, .h4, .h5, .h6 {
  margin: 0;
  padding: 0;
  float: none;
}

img, input, textarea, button, a {
  border: none;
  outline: none;
}

input, textarea, button, a {
  font-family: "Ubuntu", sans-serif;
  font-weight: normal;
  font-size: 1.11rem;
  line-height: 1;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  margin: 0;
  padding: 0;
}

td, th {
  vertical-align: top;
  word-wrap: break-word;
}

a {
  text-decoration: none;
  font-size: inherit;
  line-height: inherit; /*color: var(--blue);*/
  font-weight: inherit;
}

a:hover {
  text-decoration: underline;
}

button {
  display: block;
  cursor: pointer;
  box-sizing: border-box;
  padding: 0;
}

button, .button {
  text-decoration: none !important;
}

address {
  font-style: normal;
}

.inner {
  position: relative;
  max-width: 1585px;
  margin: 0 auto;
  box-sizing: border-box;
  padding: 0 20px;
}

section {
  position: relative;
}

main {
  position: relative;
  clear: both;
  box-sizing: border-box;
}

header, nav, section, article, aside, footer, main {
  display: block;
}

fieldset {
  position: relative;
  border: 0;
}

legend {
