.contact-elem {
  display: flex;
  flex-direction: column;
  gap: 26px;
  padding: 40px 0;
  box-sizing: border-box;
}
.contact-elem .contact-elem__caption {
  font-size: 24px;
  font-weight: 700;
}
.contact-elem .contact-elem__address {
  font-weight: 400;
  line-height: 1.4;
}
.contact-elem .contact-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.contact-elem .contact-list li {
  position: relative;
  padding-left: 32px;
  box-sizing: border-box;
}
.contact-elem .contact-list li a {
  color: var(--color);
  white-space: nowrap;
  font-size: 20px;
  transition: var(--great-transition);
}
.contact-elem .contact-list li a:hover {
  color: var(--red);
  text-decoration: none;
}
.contact-elem .contact-list li span.red {
  display: inline-block;
  margin: 0 2px;
}
.contact-elem .contact-list li:before {
  content: "";
  position: absolute;
  top: 4px;
  left: 0;
  width: 24px;
  height: 24px;
  mask-size: contain;
  mask-position: center center;
  mask-repeat: no-repeat;
  background-color: var(--red);
}
.contact-elem .contact-list li.contact-list__tel::before {
  mask-image: url(../img/phone-icon.svg);
}
.contact-elem .contact-list li.contact-list__email::before {
  mask-image: url(../img/mail-icon.svg);
}

.map-simulation {
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url(../image/map-test.jpg);
}

#map {
  position: absolute;
  top: 0;
  left: 0;
  width: 109%;
  height: 100%;
}

@media screen and (max-width: 1200px) {
  .footer {
    background-color: var(--bg);
  }
  .footer .inner {
    padding-right: 0;
    padding-left: 0;
    display: -ms-grid;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-areas: "sectionTitle " "mapBox" "contactBox";
    grid-row-gap: 0;
    grid-column-gap: 0;
  }
  .footer .section-title {
    padding: 0 20px;
    padding-top: 80px;
    margin-bottom: 30px;
    text-align: center;
    box-sizing: border-box;
  }
  .footer .map-box {
    height: 500px;
  }
  .footer .contact-box {
    padding-left: 20px;
    padding-right: 20px;
  }
  .contact-box {
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(2, 1fr);
    padding-top: 64px;
    padding-bottom: 50px;
    box-sizing: border-box;
  }
  .contact-box .contact-elem:not(:first-child) {
    border-top: none;
  }
  .contact-elem {
    padding: 0;
  }
}
@media screen and (max-width: 950px) {
  .contact-elem {
    gap: 24px;
  }
  .contact-elem .contact-elem__caption {
    font-size: 21px;
  }
  .contact-elem .contact-list li:not(:first-child) {
    margin-top: 10px;
  }
  .contact-elem .contact-list li:before {
    top: 1px;
  }
  .contact-elem .contact-list li a {
    font-size: 16px;
  }
}
@media screen and (max-width: 650px) {
  .footer .map-box {
    display: none;
  }
  .footer .section-title {
    padding-top: 48px;
    margin-bottom: 24px;
  }
  .contact-box {
    display: grid;
    grid-gap: 10px;
    grid-template-columns: 1fr;
    padding-top: 0;
    padding-bottom: 48px;
    box-sizing: border-box;
  }
  .contact-elem {
    padding: 20px;
    border-radius: 8px;
    gap: 16px;
    background-color: var(--bg-alt);
  }
  .contact-elem .contact-elem__caption {
    font-size: 20px;
  }
  .contact-elem .contact-elem__address {
    font-size: 16px;
  }
  .contact-elem .btn {
    width: 100%;
  }
}
@media screen and (max-width: 600px) {
  .footer .section-title, .footer .contact-box {
    padding-left: 14px;
    padding-right: 14px;
  }
}
.great-shadow {
  position: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 2rem;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(65, 73, 82, 0.5);
  backdrop-filter: blur(5px);
  z-index: 2000;
  transition: var(--great-transition);
  overflow: hidden;
  overflow-y: auto;
  box-sizing: border-box;
  opacity: 0;
  visibility: hidden;
}
.great-shadow.show {
  opacity: 1;
  visibility: visible;
}

.modal {
  position: relative;
  width: 100%;
  margin-top: auto;
  margin-bottom: auto;
  border-radius: 10px;
  background-color: #121C27;
  max-width: 500px;
  transition: var(--great-transition);
  padding: 16px 24px;
  padding-bottom: 40px;
  opacity: 0;
  visibility: hidden;
  overflow: hidden;
}
.modal .modal__header {
  display: flex;
  gap: 16px;
  align-items: flex-start;
  margin-bottom: 16px;
}
.modal .modal__header .modal__caption {
  flex-grow: 1;
}
.modal .modal__caption {
  display: block;
  font-size: 24px;
  font-weight: 500;
  text-transform: uppercase;
}
.modal .modal__close-btn {
  position: relative;
  width: 24px;
  height: 24px;
  background-color: transparent;
}
.modal .modal__close-btn::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 85%;
  height: 1.5px;
  border-radius: 3px;
  background-color: var(--red);
  transform: translate(-50%, -50%) rotate(45deg);
  transition: var(--great-transition);
}
.modal .modal__close-btn::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 85%;
  height: 1.5px;
  border-radius: 3px;
  background-color: var(--red);
  transform: translate(-50%, -50%) rotate(-45deg);
  transition: var(--great-transition);
}
.modal .modal__close-btn:hover::before, .modal .modal__close-btn:hover::after {
  background-color: #F6294C;
}
.modal .modal__description {
  font-size: 16px;
}
.modal .send-alert {
  display: block;
  font-size: 12px;
  margin-top: 16px;
  color: #A0A4A9;
}
.modal .modal__success-message {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 24px;
  background-color: #121C27;
  font-size: 24px;
  font-weight: 400;
  line-height: 1.6;
  text-align: center;
  box-sizing: border-box;
  opacity: 0;
  visibility: hidden;
}
.modal--success-answer .modal__success-message {
  opacity: 1;
  visibility: visible;
}
.modal.show {
  opacity: 1;
  visibility: visible;
}

.hely-modal-form {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-top: 30px;
}
.hely-modal-form .btn {
  width: 100%;
  margin-top: 14px;
}

.heli-input {
  display: block;
  width: 100%;
  border-radius: 8px;
  padding: 0 16px;
  border: 1px solid #414952;
  min-height: 50px;
  background-color: transparent;
  font-size: 16px;
  font-weight: 400;
  line-height: 1;
  box-sizing: border-box;
  transition: var(--great-transition);
  color: var(--color);
}
.heli-input::-webkit-input-placeholder {
  opacity: 1;
  transition: opacity 0.3s ease;
  color: #A0A4A9;
}
.heli-input::-moz-placeholder {
  opacity: 1;
  transition: opacity 0.3s ease;
  color: #A0A4A9;
}
.heli-input:-moz-placeholder {
  opacity: 1;
  transition: opacity 0.3s ease;
  color: #A0A4A9;
}
.heli-input:-ms-input-placeholder {
  opacity: 1;
  transition: opacity 0.3s ease;
  color: #A0A4A9;
}
.heli-input:focus::-webkit-input-placeholder {
  opacity: 0;
  transition: opacity 0.3s ease;
}
.heli-input:focus::-moz-placeholder {
  opacity: 0;
  transition: opacity 0.3s ease;
}
.heli-input:focus:-moz-placeholder {
  opacity: 0;
  transition: opacity 0.3s ease;
}
.heli-input:focus:-ms-input-placeholder {
  opacity: 0;
  transition: opacity 0.3s ease;
}
.heli-input:focus, .heli-input:active {
  border: 1px solid var(--red);
}

.heli-textarea {
  display: block;
  width: 100%;
  border-radius: 8px;
  padding: 16px;
  border: 1px solid #414952;
  min-height: 104px;
  background-color: transparent;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.4;
  box-sizing: border-box;
  transition: var(--great-transition);
  color: var(--color);
  resize: none;
}
.heli-textarea::-webkit-input-placeholder {
  opacity: 1;
  transition: opacity 0.3s ease;
  color: #A0A4A9;
}
.heli-textarea::-moz-placeholder {
  opacity: 1;
  transition: opacity 0.3s ease;
  color: #A0A4A9;
}
.heli-textarea:-moz-placeholder {
  opacity: 1;
  transition: opacity 0.3s ease;
  color: #A0A4A9;
}
.heli-textarea:-ms-input-placeholder {
  opacity: 1;
  transition: opacity 0.3s ease;
  color: #A0A4A9;
}
.heli-textarea:focus::-webkit-input-placeholder {
  opacity: 0;
  transition: opacity 0.3s ease;
}
.heli-textarea:focus::-moz-placeholder {
  opacity: 0;
  transition: opacity 0.3s ease;
}
.heli-textarea:focus:-moz-placeholder {
  opacity: 0;
  transition: opacity 0.3s ease;
}
.heli-textarea:focus:-ms-input-placeholder {
  opacity: 0;
  transition: opacity 0.3s ease;
}
.heli-textarea:focus, .heli-textarea:active {
  border: 1px solid var(--red);
}

.phone-box {
  display: flex;
}
.phone-box .heli-input {
  order: 2;
  border-radius: 0 8px 8px 0;
}
.phone-box .choices {
  order: 1;
  margin-bottom: 0;
  max-width: 84px;
  width: 100%;
}
.phone-box .choices .choices__inner {
  display: inline-block;
  vertical-align: top;
  width: 100%;
  background-color: #121c27;
  padding: 7.5px 16px 3.75px;
  padding-right: 30px;
  border: 1px solid #414952;
  border-radius: 0;
  border-right: 0;
  border-radius: 8px 0 0 8px;
  font-size: 16px;
  min-height: 50px;
  overflow: hidden;
  box-sizing: border-box;
  transition: var(--great-transition);
}
.phone-box .choices .choices__list--single {
  display: inline-block;
  padding: 4px 0px;
  width: 100%;
}
.phone-box .choices[data-type*=select-one]::after {
  width: 14px;
  height: 14px;
  box-sizing: border-box;
  border: none;
  background-color: var(--color);
  margin-top: 0;
  mask-image: url(../img/choices-arrow.svg);
  mask-position: center center;
  mask-repeat: no-repeat;
  mask-size: contain;
  transform: translateY(-50%) scale(1);
  transition: var(--great-transition);
}
.phone-box .choices .choices__list--single .choices__item {
  color: var(--red);
  font-weight: 400;
}
.phone-box .choices .choices__list.choices__list--dropdown {
  background-color: #121c27;
  border: 1px solid #414952;
  width: calc(100% + 1px);
  box-sizing: border-box;
}
.phone-box .choices .choices__list.choices__list--dropdown .choices__item {
  background-color: #121c27;
  padding: 10px 16px;
  font-size: 16px;
  font-weight: 400;
  box-sizing: border-box;
  transition: var(--great-transition);
}
.phone-box .choices .choices__list.choices__list--dropdown .choices__item.is-highlighted {
  background-color: var(--bg-alt);
}
.phone-box .choices .choices__list.choices__list--dropdown .choices__item.is-selected {
  color: var(--red);
}
.phone-box .choices .choices__list.choices__list--dropdown .choices__item:after {
  display: none;
}
.phone-box .choices.is-open[data-type*=select-one]::after {
  transform: translateY(-50%) scale(-1);
}
.phone-box .choices.is-open .choices__inner {
  border-radius: 8px 0 0 0;
}
.phone-box .heli-input:active ~ .choices .choices__inner,
.phone-box .heli-input:focus ~ .choices .choices__inner {
  border: 1px solid var(--red);
  border-right: 0;
}

